@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
}

.navbar {
  background-color: #fff !important;
}
.navbar-brand {
  font-size: 1.9rem !important;
  color: #565387 !important;
}

.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}
.navbar a:hover {
  color: red !important;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

#header .brand-name {
  color: #3498db;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background: #3498db;
  color: #fff;
}

.header-img {
  text-align: right;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 1rem;
  text-align: center;
}

h2,
h1 {
  font-size: 25px;
}

.foot {
  display: flex;
  position: fixed;
  text-align: center;
  bottom: 0 !important;
  width: 100% !important;
  padding: 1rem !important;
  background-color: paleturquoise;
  justify-content: center;
  align-items: center;
  height: 20px;
}

p {
  margin: 4px auto;
}

.footBottom {
  position: fixed;
}

h1 {
  margin: 65px auto;
}
